import React from "react";
import { Field, reduxForm } from "redux-form";
import Post from "./../Post";
import style from "./../Profile.module.css";
import { InputField} from "../../common/FormsControls/FormsControls";

let Wall = (props) => {
  const onAddPost = (value) => {
    props.AddPost(value.NewPostProfile);
  };

  let ElementPostData = props.postData.map((post) => (
    <Post
      key={post.key}
      photo={post.photo}
      name={post.name}
      message={post.message}
      likesCount={post.likesCount}
    />
  )).reverse();
  return (
    <div >
      <div> :</div>
      <div>
      <PostDataForm onSubmit={onAddPost} />
      </div>
      {!props.isOwner ? <div>{ElementPostData}</div>: <span>   </span>}
    </div>
  );
};

const addNewPostForm = (props) => {
  return (
    <div>
      <form onSubmit={props.handleSubmit}>
        <div>
          <Field className={style.wall__input}  
            placeholder="Write your post..."
            component={InputField}
            name={"NewPostProfile"}
          />
        </div>
        <div>
          <button className={style.general__button}>Send post</button>
        </div>
      </form>
    </div>
  );
};

const PostDataForm = reduxForm({ form: "posProfileAddNewPostFormtData" })(
  addNewPostForm
);

export default Wall;
